body{
  background:black;
}
.myDiv {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 50vh; /* 25% of the viewport height */
  width: 100%;
  background-size: 100%;
  background-position: 0% 50%; /* Horizontal: 50% | Vertical: 0 */
  background-repeat: no-repeat;
  /* Add any other styles you need */
}

@keyframes scroll {
  0% {
    transform: translateX(0%);
  }
  100% {
    transform: translateX(-10%);
  }
}

.scroll-text {
  color:white;
  white-space: nowrap;
  overflow: hidden;
  animation: scroll 120s linear infinite;
  display: inline-block;
  font-family: Helvetica, Arial, sans-serif;
  font-weight: bold;
  font-size: 5vw;
  margin:0;
}
.button-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.centered-buttons {
  display: flex;
  align-items: center;
  justify-content: center;
}

/* CSS */
.button-79 {
  backface-visibility: hidden;
  background: #000000;
  outline: 1px solid white;
  box-shadow: 0 0 0 1px white;
  border: 0;
  border-radius: .375rem;
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  font-family: Circular,Helvetica,sans-serif;
  font-size: 2vw;
  font-weight: 700;
  letter-spacing: -.01vw;
  line-height: 1.3;
  padding: 1vw 1vw;
}
.button-79:disabled {
  color: #787878;
  cursor: auto;
}

.button-79:not(:disabled):hover {
  transform: scale(1.05);
}

.button-79:not(:disabled):hover:active {
  transform: scale(1.05) translateY(.125rem);
}

.button-79:focus {
  outline: 0 solid transparent;
}

.button-79:focus:before {
  border-width: .125rem;
  content: "";
  left: calc(-1*.375rem);
  pointer-events: none;
  position: absolute;
  top: calc(-1*.375rem);
  transition: border-radius;
  user-select: none;
}

.button-79:focus:not(:focus-visible) {
  outline: 0 solid transparent;
}

.button-79:not(:disabled):active {
  transform: translateY(.125rem);
}
.centered-buttons > * {
  margin:2vw;
}
