}
  .image-grid {
    display: flex;
    flex-wrap: wrap;
  }
  
  .grid-item {
    width: 25vw; /* Each item takes 25% of the container width to create a square */
    height: 25vw;
    padding: 10px;
    box-sizing: border-box;
    overflow: hidden;
  }
  
  .image-container {
    width: 100%;
    padding-bottom: 100%; /* Maintain aspect ratio for square shape */
    position: relative;
  }
  
  .image {
    position: absolute;
    top: -50%;
    left: -50%;
    object-fit: cover; /* Scale the image to fit without distortion */
  }
  .todo {
    display: flex;
    align-items: center;
  }
  
  @media (max-width: 767px) {
    /* Styles for cellphones */
    .todo {
      flex-direction: column;
    }
  }
  
  @media (min-width: 768px) {
    /* Styles for wider screens */
    .todo {
      flex-direction: row;
    }
  }
  p {
    backface-visibility: hidden;
